<template>
  <div>
    <v-text-field label="M H s" v-model="code" outlined append-icon="mdi-close-circle"></v-text-field>
    <v-textarea class="textarea" type="text" id="igateToken" v-model="igateToken" placeholder="igateToken"
      required></v-textarea>
    <v-btn @click="runProcess" style="
                color: white;
                font-weight: bold;
                width: auto;
                padding: 10px;
                margin: 10px 10px;
                border: 1px solid #bbbbbb !important;
                border-radius: 4px;
                box-sizing: border-box;
                font-size: 16px;
                background-color: #28a745 !important;
              ">Ly kt qu</v-btn>
    <p v-if="log.length">
      <strong>Log:</strong>
    <ul>
      <li v-for="(item, index) in log" :key="index">{{ item }}</li>
    </ul>
    </p>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      token: "Bearer", //  Thay bng token thc t
      code: "",
      maxRetries: 5,
      log: [],
      igateToken: "",
    };
  },
  methods: {
    async getObject1() {
      const maHsoTrimmed = this.code.trim().replace(/\s+/g, "");
      const url = `https://apiigate.gialai.gov.vn/pa/api-integration/--data?code=${maHsoTrimmed}&fields=commonEForm%2CdetailEForm`;
      const res = await axios.get(url, {
        headers: { Authorization: `Bearer ${this.igateToken}` },
      });
      return res.data;
    },

    async callDossierAPI(object1) {
      const url = `https://apiigate.gialai.gov.vn/ad/service/judicial-civil-status/--dossiers`;
      const body = {
        eformData: {
          noiDangKy: object1.detailEForm.data.noiDangKy,
        },
        maDinhDanhHoSo: [object1.code],
        module: object1.detailEForm.data.loaiHTTP,
        maTinh: "52",
      };

      const res = await axios.post(url, body, {
        headers: {
          Authorization: `Bearer ${this.igateToken}`,
          "Content-Type": "application/json",
        },
      });
      return res.data;
    },

    async getStatusLog(nationCode) {
      const url = `https://apiigate.gialai.gov.vn/ad/api/lienthongDVCLT/getLog?api=capNhatTrangThaiHoSoDVCLTHoTich&nationCode=${nationCode}`;
      const res = await axios.get(url, {
        headers: {
          Authorization: `Bearer ${this.igateToken}`,
        },
      });
      return res.data.content;
    },

    async updateStatusFromLog(logItem, trangThai, ghiChu) {
      const body = {
        ...logItem.requestBody,
        trangThai: trangThai,
        ghiChu: ghiChu,
      };

      const url = `https://apiigate.gialai.gov.vn/integration/api/lienthongDVCLT/capNhatTrangThaiHoSoDVCLTHoTich`;
      const res = await axios.post(url, body, {
        headers: {
          Authorization: `Bearer ${this.igateToken}`,
          "Content-Type": "application/json",
        },
      });
      return res.data;
    },

    async ensureStatuses(object1) {
      const requiredStatuses = {
        1: "Tip nhn h s vo h thng",
        3: "H s  iu kin gii quyt",
        7: " chuyn cn b t php",
      };

      const logs = await this.getStatusLog(object1.nationCode);
      const existingStatuses = logs.map((log) => log.requestBody.trangThai);

      //  Nu  c trng thi 5 th b qua lun
      if (existingStatuses.includes(5)) {
        this.log.push(
          "  c trng thi 5  B qua cp nht trng thi 1,3,7."
        );
        return;
      }

      //  Nu cha c 5, th m bo c  1,3,7
      for (const [status, ghiChu] of Object.entries(requiredStatuses)) {
        if (!existingStatuses.includes(Number(status))) {
          this.log.push(
            ` Thiu trng thi ${status}, ang gi API cp nht...`
          );
          const sourceLog = logs[0]; // ly log u tin lm mu
          await this.updateStatusFromLog(sourceLog, Number(status), ghiChu);
          this.log.push(`  cp nht trng thi ${status}`);
          await new Promise((resolve) => setTimeout(resolve, 1000));
        } else {
          this.log.push(`  c trng thi ${status}`);
        }
      }
    },
    async ensureStatusesFromLogs(logs) {
      const requiredStatuses = {
        1: "Tip nhn h s vo h thng",
        3: "H s  iu kin gii quyt",
        7: " chuyn cn b t php",
      };

      const existingStatuses = logs.map((log) => log.requestBody.trangThai);

      for (const [status, ghiChu] of Object.entries(requiredStatuses)) {
        if (!existingStatuses.includes(Number(status))) {
          this.log.push(
            ` Thiu trng thi ${status}, ang gi API cp nht...`
          );
          const sourceLog = logs[0]; // dng log u tin lm mu
          await this.updateStatusFromLog(sourceLog, Number(status), ghiChu);
          this.log.push(`  cp nht trng thi ${status}`);
          await new Promise((resolve) => setTimeout(resolve, 1000));
        } else {
          this.log.push(`  c trng thi ${status}`);
        }
      }
    },

    async callSyncAPI(code) {
      const url = `https://apiigate.gialai.gov.vn/pa/judicial-civil-status/--sync-dossiers?new=true&code=${code}&showInputSend=keytest_8957`;
      const res = await axios.post(url, null, {
        headers: {
          Authorization: `Bearer ${this.igateToken}`,
          "Content-Type": "application/json",
        },
      });
      return res.data;
    },

   async waitForTrangThai4(object1) {
  const maxRetries = this.maxRetries;

  for (let i = 0; i < maxRetries; i++) {
    this.log.push(` ng b ln ${i + 1}...`);

    const syncResult = await this.callSyncAPI(object1.code);

    const checkTrangThai4 = (list) => {
      return list?.some(item =>
        item?.result?.value?.some(val => val.trangThai === 4)
      );
    };

    const extractTrangThaiList = (list, label) => {
      if (!Array.isArray(list)) return;
      list.forEach(item => {
        const values = item?.result?.value || [];
        values.forEach(val => {
          this.log.push(` [${label}] m h s: ${val.maHoSoMCDT || "?"}  trng thi: ${val.trangThai}`);
        });
      });
    };

    //  In cc trng thi ang c ra log
    extractTrangThaiList(syncResult.List_Suss, "SUCCESS");
    extractTrangThaiList(syncResult.List_ERR, "ERROR");

    const foundInSuss = checkTrangThai4(syncResult.List_Suss || []);
    const foundInErr = checkTrangThai4(syncResult.List_ERR || []);

    if (foundInSuss || foundInErr) {
      this.log.push("  nhn c trng thi 4 trong ng b.");
      return;
    }

    if (i === maxRetries - 1) {
      this.log.push("  th 5 ln nhng khng nhn c trng thi 4. H s c th cha ban hnh bn h tch hoc cn b nhp tay.");
    }

    await new Promise((resolve) => setTimeout(resolve, 3000));
  }
},



    async runProcess() {
      this.log = [];
      try {
        this.log.push(" ang ly thng tin h s...");
        const object1 = await this.getObject1();

        //  Ly danh sch trng thi log
        const logs = await this.getStatusLog(object1.nationCode);
        const existingStatuses = logs.map((log) => log.requestBody.trangThai);

        //  Nu  c trng thi 5 th kt thc sm
        if (existingStatuses.includes(5)) {
          this.log.push(
            " H s  c trng thi 5  Khng cn thc hin g thm."
          );
          return;
        }

        //  Nu cha c trng thi 5  x l m bo  1, 3, 7
        await this.ensureStatusesFromLogs(logs);
//  Bt u vng lp ch trng thi 4
        this.log.push(" Bt u gi API ng b  kim tra trng thi 4...");
await this.waitForTrangThai4(object1);

        //  Bt u vng lp ch trng thi 4
        // for (let i = 0; i < this.maxRetries; i++) {
        //   this.log.push(` Ln ${i + 1}: Gi API ly kt qu...`);
        //   const result = await this.callDossierAPI(object1);
        //   this.log.push("M h s lin thng:", object1.nationCode);

        //   const trangThai = result?.value?.[0]?.trangThai;
        //   this.log.push(` Trng thi tr v: ${trangThai}`);

        //   if (trangThai === 4) {
        //     this.log.push(
        //       "  nhn trng thi 4! Gi API ng b mt ca..."
        //     );
        //     const syncResult = await this.callSyncAPI(object1.code);
        //     this.log.push(" API ng b tr v thnh cng.");
        //     break;
        //   }

        //   if (i === this.maxRetries - 1) {
        //     this.log.push(
        //       "  th 5 ln nhng khng nhn c trng thi 4. C th cha ban hnh bn h tch hoc thiu trng thi 1,3,7"
        //     );
        //   }

        //   await new Promise((resolve) => setTimeout(resolve, 3000));
        // }
      } catch (err) {
        this.log.push(` Li: ${err.response?.data?.message || err.message}`);
      }
    },
  },
};
</script>
<style scoped>
textarea {
  width: 100%;
  /* Chiu rng 100% ca phn t cha */
  height: 100px;
  /* Chiu cao c th */
  box-sizing: border-box;
  /* m bo kch thc bao gm padding v border */
  border: 1px solid #bbbbbb !important;
}
</style>